<template>
    <FloatingPanel :width="width" :height="height"  @close="$emit('close')">
        <div v-if="isLoading" class="loading">
            <h1>Loading data...</h1>    
        </div>
        <div>
            ចម្ងាយពីទន្លេ៖ 
            <input class="buffer-input" type="text" id="buffer-km" v-model="bufferSize" @keypress="isNumber($event)">
            គ.ម
            <input type="button" value="Run" @click="onRun">
        </div>
        
    </FloatingPanel>
</template>

<script>
const t = require('@turf/turf');
import FloatingPanel from "@/components/FloatingPanel.vue";
import func from '@/scripts/helpers.js';
import events from "@degjs/event-aggregator";
import store from "../store";

export default {
    name: 'SandProjects',

    props:{
        width: {
            type: Number,
            default: 600
        },
        height: {
            type: Number,
            default: 200
        }
    },

    components: {
        FloatingPanel
    },
    
    async beforeCreate() {
        const river = await func.fetch.riverAsync();
        this.river = river;
        this.$emit('river-loaded', river);
        this.isLoading = false;
    },

    data() {
        const river = func.geojson.empty();
        return {
            river,
            isLoading: true,
            bufferSize: 1.5
        }
    },

    computed:{
        isEmptyRiver(){
            return func.geojson.isEmpty(this.river);
        },
        bufferedZone(){
            return this.isEmptyRiver? this.river: t.buffer(this.river, this.bufferSize);
        },
        selectedProjects(){
            const projects = store.getters.geojson;
            const features = projects.features;
            console.log(features);
            const bufferedZone = this.bufferedZone;
            console.log(bufferedZone);
            const selected = features.filter(f => t.booleanIntersects(f, bufferedZone));
            console.log(selected);
        }

    },

    methods:{
        isIntersect(testFeature, boundFeature) {
            
            const overlap = t.booleanIntersects(testFeature, boundFeature);
            return overlap;
        },
        isNumber: function(evt) {
            evt = (evt) ? evt : window.event;
            var charCode = (evt.which) ? evt.which : evt.keyCode;
            if ((charCode > 31 && (charCode < 48 || charCode > 57)) && charCode !== 46) {
                evt.preventDefault();;
            } else {
                return true;
            }
        },
        onRun(){
            console.log('selected: ', this.selectedProjects);
            //const selected = features.filter(f => t.booleanOverlap(f, this.bufferedZone));
            //console.log(selected);
        }
    }


}
</script>

<style scoped>
.loading {
    text-align: center;
    margin: 9em;
}

.buffer-input {
    width: 50px;
    height: 20px;
    text-align: center;
}
</style>